<template>
  <div class="dataAuditExtrackApply ">
    <div class="topTitle">
      <span class="topTitle-span1">审核任务</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">业务洗钱风险审核任务</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">调整待审核</span>
    </div>
    <div class="contentCellBox">
      <Tabs v-model="tabname" @on-click="resetRequest">
        <TabPane label="待审核" name="name1">
          <div class="contentCellBox">
            <div class="contentCellBody1">
              <Form
                ref="formValidate"
                :model="formValidate"
                :rules="ruleValidate"
                :label-width="120"
                inline
              >
                <Row>
                  <FormItem label="业务部门：" prop="extraApplicantUnit">
                    <treeselect
                      v-model="formValidate.extraApplicantUnit"
                      valueConsistsOf="ALL" 
              :limit="3"
                      :multiple="true"
                      :options="data1"
                      placeholder
                      style="width:200px"
                    />
                  </FormItem>
                    <FormItem label="申请人：" prop="formname">
                    <Input v-model="formValidate.formname" placeholder="请输入申请人" style="width:200px"></Input>
                  </FormItem>
                  <FormItem label="申请时间：" >
                    <DatePicker
                      type="daterange"
                      placeholder="请选择申请时间"
                      :value="formValidate.permissionTime"
                      @on-change="formValidate.permissionTime=$event"
                      style="width:200px"
                ></DatePicker>
                  </FormItem>
                </Row>
                <Row>
                  <div class="btnClass">
                    <Button
                      type="primary"
                      @click="handleSubmit('formValidate','1','0')"
                      style="width:89px"
                    >查询</Button>
                    <Button
                      type="primary"
                      style="margin-left:10px"
                      @click="handleSubmit('formValidate','2','0')"
                    >精确查询</Button>
                  </div>
                </Row>
              </Form>
            </div>
          </div>
          <div class="contentCellBox">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>数据列表
            </div>
            <div class="contentCellBody">
              <Row>
                <table class="tableBox">
                  <thead>
                    <tr>
                      <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item,index) in tableBody" :id="item.documentId">
                      <td>
                        {{pageSize*(pageNum-1)+index+1}}
                      </td>
                      <td>
                        <span class="spanBox textMaxWidth textOverflow" @click="showDialog(item)" :title="item.taskname">{{item.taskname}}</span>
                      </td>
                      <td>{{item.department}}</td>
                      <td>{{item.applicant}}</td>
                      <td>{{item.applyTime?item.applyTime.substr(0,19):''}}</td>
                      <td>
                        <span class="operationSpan" @click="verifyDetail(item.documentId)">审核</span>
                        <span class="operationSpan" @click="watchFiles(item.documentId)">查看</span>
                        <span class="operationSpan" @click="flowDetail(item.documentId)">流程详情</span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </Row>
            </div>
            <div class="flexCenter pageBox">
              <Page :current.sync="pageNum" :total="rowCount" :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun' show-elevator show-sizer placement='top'/>
            </div>
          </div>
        </TabPane>
        <TabPane label="已审核" name="name2">
          <div class="contentCellBox">
            <div class="contentCellBody1">
              <Form
                ref="formValidate1"
                :model="formValidate1"
                :rules="ruleValidate1"
                :label-width="120"
                inline
              >
                <Row>
                  <FormItem label="业务部门：" prop="extraApplicantUnit">
                    <treeselect
                      v-model="formValidate1.extraApplicantUnit"
                      valueConsistsOf="ALL" 
              :limit="3"
                      :multiple="true"
                      :options="data2"
                      placeholder
                      style="width:200px"
                    />
                  </FormItem>
                  <FormItem label="申请人：" prop="formname">
                    <Input v-model="formValidate1.formname" placeholder="请输入申请人" style="width:200px"></Input>
                  </FormItem>
                  <FormItem label="申请时间：" >
                    <DatePicker
                      type="daterange"
                      placeholder="请选择申请时间"
                      :value="formValidate1.permissionTime"
                      @on-change="formValidate1.permissionTime=$event"
                ></DatePicker>
                  </FormItem>
                </Row>
                <Row>
                  <div class="btnClass">
                    <Button
                      type="primary"
                      @click="handleSubmit('formValidate1','1','1')"
                      style="width:89px"
                    >查询</Button>
                    <Button
                      type="primary"
                      style="margin-left:10px"
                      @click="handleSubmit('formValidate1','2','1')"
                    >精确查询</Button>
                  </div>
                </Row>
              </Form>
            </div>
          </div>
          <div class="contentCellBox">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>数据列表
            </div>
            <div class="contentCellBody">
              <Row>
                <table class="tableBox">
                  <thead>
                    <tr>
                      <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item,index) in tableBody2" :id="item.documentId">
                      <td>
                        {{pageSize2*(pageNum2-1)+index+1}}
                      </td>
                      <td>
                        <span class="spanBox textMaxWidth textOverflow" @click="showDialog(item)" :title="item.taskname">{{item.taskname}}</span>
                      </td>
                      <td>{{item.department}}</td>
                      <td>{{item.applicant}}</td>
                      <td>{{item.applyTime?item.applyTime.substr(0,19):''}}</td>
                      <td>
                        <span class="operationSpan" @click="watchFiles(item.documentId)">查看</span>
                        <span class="operationSpan" @click="flowDetail(item.documentId)">流程详情</span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </Row>
            </div>
            <div class="flexCenter pageBox">
              <Page
                :total="rowCount2"
                :current.sync="pageNum2"
                :page-size="pageSize2"
                @on-change="pageChange2"
                @on-page-size-change='pageSizefun2'
                show-elevator
                show-sizer
                placement='top'
              />
            </div>
          </div>
        </TabPane>
        <!-- <Button type="primary" class="rightTopbtn" slot="extra">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button> -->
      </Tabs>
      <Modal :mask-closable="false"  
      v-model="showModal"
      title="任务详情"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}">
      <component :is="tabview"  :key='batchId' 
       :data-objj="dataObjj"
       :dataObj="dataObj" width="80%"></component>
      <div slot="footer"></div>
    </Modal>
    <Modal :mask-closable="false"   v-model="modal1" title="文件列表"  :styles="{width: '40%'}">
        <Table border :columns="columns1" :data="fileList"></Table>
        <div slot="footer">
        </div>
    </Modal>
    </div>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
import processMessage from '@/components/moneyRiskAssess/common/processMessage.vue';//过程信息
import assessDetail from "@/components/moneyRiskAssess/common/assessDetail"; //评估


export default {
  components:{
    Treeselect,
    processMessage,
    assessDetail
  },
  created() {
      this.getExtractDic();
      this.handleSubmit('formValidate', '1', this.tabname=="name1"?'0':'1')
      this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          this.data1 = response.data;
          this.selectTreeCleanUp(this.data1);
        }
      })
      .catch(error => {});
       this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          this.data2 = response.data;
          this.selectTreeCleanUp(this.data2);
        }
      })
      .catch(error => {});
  },
  data() {
    return {
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "上传人",
          key: "creatorName"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index);
                    }
                  }
                },
                "下载"
              ),
            ]);
          }
        }
      ],
      modal1:false,
      fileList: [],
      batchId:'',
      showModal:false,
      tabview:'processMessage',
      dataObj:{},
      dataObjj:{},
      type:'',
      data1: [],
      data2: [],
      busiSelect: [
        //业务范围下拉
      ],
      extendFlaf: "1",
      flagSlect: "1", //查询方式
      rowCount: 0, //待审核条数
      pageSize: 10, //待审核页量
      pageNum: 1,
      rowCount2: 0, //已审核条数
      pageSize2: 10, //已审核页量
      pageNum2: 1,
      tabname: "name1",
      busiSCodes: [],
      queryData: {},
      tableHeader: [
        { name: "序" },
        { name: "任务名称" },
        { name: "业务部门" },
        { name: "申请人" },
        { name: "申请时间" },
        { name: "操作" }
      ],
      tableBody: [],
      tableBody2: [],
      formValidate: {
        extraApplicantUnit: [], //业务部门
        formname: "", //申请人
        permissionTime: "", //申请时间
      },
      formValidate1: {
        extraApplicantUnit: [], //业务部门
        formname: "", //申请人
        permissionTime: "", //申请时间
      },
      ruleValidate: {
        extraApplicantUnit: [
          {
            required: false,
            message: "请选择业务",
            trigger: "change"
          }
        ],
        formname: [{ required: false, message: "请输入", trigger: "blur" }],
        permissionTime: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ],
      },
      ruleValidate1: {
        extraApplicantUnit: [
          {
            required: false,
            message: "请选择业务",
            trigger: "change"
          }
        ],
        formname: [{ required: false, message: "请输入", trigger: "blur" }],
        permissionTime: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ],
      }
    };
  },
  methods: {
    fileDownload(index) {
      let multData = new FormData();
      let fileName = this.fileList[index].name;
      multData.append("fileId", this.fileList[index].id);
      this.axios
        .post(`/bpm/download`, multData, {
          headers: { "Content-Type": "multipart/form-data" },
          responseType: "blob"
        })
        .then(res => {
          this.download(res, fileName);
        })
        .catch(function(error) {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    watchFiles(documentId){
      let multData = new FormData();
      multData.append("documentId", documentId);
      this.axios
        .post("/bpm/list", multData, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            this.modal1 = true;
            this.fileList = response.data.dataVo.files;
          }
        })
        .catch(error => {});
    },
    cancel(){},
    pageSizefun(pageNo){
      this.pageSize = pageNo * 1;
      if(this.pageNum == 1){
          this.pageChange(1);
      }
    },
    pageSizefun2(pageNo){
      this.pageSize2 = pageNo * 1;
      if(this.pageNum2 == 1){
          this.pageChange2(1);
      }
    },
    showDialog(item){
      console.log('item', item)
      this.showModal = true;
      this.tabview = 'assessDetail';
      this.batchId = item.busId;
      this.dataObjj = {
        busId: item.busId,
        newBusId: "",
        type: "",
        evalBusinessId: "",
        adjBusinessId: ""
      };
    },
    selectTreeCleanUp(treeDatas) {
        for (let i = 0; i < treeDatas.length; i++) {
          if (treeDatas[i].children && treeDatas[i].children.length >0) {
              this.selectTreeCleanUp(treeDatas[i].children);
          }
          else this.$delete(treeDatas[i], "children");
        }
    },
    // 获取字典的页面
    getExtractDic(){
      this.axios
      .get(
        "/extract/getExtractDic/EXTRACT_UNIT,BUSI_SCOPE,EXECU_STATUS"
      )
      .then((response)=> {
        if (response.code == "200") {
          this.busiSelect = response.data.BUSI_SCOPE;
        }
      })
      .catch(function(error) {});
    },
    verifyDetail(documentId) {
      let { href } = this.$router.resolve({
          name: "busAuditExtrackAdjustDetail",
          query: {
            documentId
          }
        });
        window.open(href, "_blank");
    },
    flowDetail(documentId){
      this.batchId = documentId;
      this.dataObj = {
        documentId,
        name:'业务洗钱风险'
      }
      this.tabview = 'processMessage';
      this.showModal = true;
    },
    /**
     * name:formValidate 待审核查询条件 formValidate2 已审核查询条件 
     * exactquery：1、普通查询 2、精确查询
     * extend：0、待审核 1、已审核
     */
    handleSubmit(name, exactquery, extend) {
      this.flagSlect = exactquery;
      let that = this;
      let data = {
        extend: extend,
        pageNum: 1,
        pageSize: extend=="0"?this.pageSize:this.pageSize2,
        flag: exactquery,
        department: JSON.stringify(this[name].extraApplicantUnit), //业务部门
        applicant: this[name].formname.trim(), //申请人
        applyTime: this[name].permissionTime[0], //申请时间
        applyTime1: this[name].permissionTime[1],
      };
      data = JSON.stringify(data);
      this.axios
        .post("/risk/task/doBusRiskAdjustTaskList", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.data && response.data.records) {
            if(extend == 0){
              that.rowCount = response.data.total;
              that.tableBody = response.data.records;
            }else if(extend == 1){
              that.rowCount2 = response.data.total;
              that.tableBody2 = response.data.records;
            }
          }
        })
        .catch(error => {});
    },
    resetRequest(name) {
      //tab切换
      if (name == "name1") {
        this.pageSize = 10;
        this.handleSubmit('formValidate', '1', '0')
        //发起第一个页请求
      } else if (name == "name2") {
        this.pageSize2 = 10;
        this.handleSubmit('formValidate1', '1', '1')
      }
    },
    pageChange(pageNo) {
      let that = this;
      this.pageNum = pageNo;
      let data = {
        extend: "0",
        pageNum: pageNo,
        pageSize: this.pageSize,
        flag: this.flagSlect,
        department: JSON.stringify(this.formValidate.extraApplicantUnit), //业务部门
        applicant: this.formValidate.formname.trim(), //申请人
        applyTime: this.formValidate.permissionTime[0], //申请时间
        applyTime1: this.formValidate.permissionTime[1],
      };
      this.axios
        .post("/risk/task/doBusRiskAdjustTaskList", JSON.stringify(data), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.data) {
            that.rowCount = response.data.total;
          }
          if (response.data.records) {
            that.tableBody = response.data.records;
          }
        })
        .catch(error => {});
    },
    pageChange2(pageNo) {
      let that = this;
      this.pageNum2 = pageNo;
      let data = {
        extend: "1",
        pageNum: pageNo,
        pageSize: this.pageSize2,
        flag: this.flagSlect,
        department: JSON.stringify(this.formValidate1.extraApplicantUnit), //业务部门
        applicant: this.formValidate1.formname.trim(), //申请人
        applyTime: this.formValidate1.permissionTime[0], //申请时间
        applyTime1: this.formValidate1.permissionTime[1],
      };
      this.axios
        .post("/risk/task/doBusRiskAdjustTaskList", JSON.stringify(data), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.data) {
            that.rowCount2 = response.data.total;
          }
          if (response.data.records) {
            that.tableBody2 = response.data.records;
          }
        })
        .catch(error => {});
    }
  }
};
</script>
<style lang="scss" scoped>
.dataAuditExtrackApply{
  .rightTopbtn {
    margin: 0px 5px;
    i.ivu-icon{
      font-size: 16px;
      vertical-align: -3px;
      padding-right: 2px;
    }
  }
  .btnClass {
    display: flex;
    justify-content: center;
    padding: 20px 0;
  }
  .operationSpan {
    color: blue;
    margin: 0 5px;
    cursor: pointer;
  }
}
.dataAuditExtrackApply{
  min-height: 100vh;
}
</style>



